.admin_form {
  display: grid;
  column-gap: 30px;
}
.admin_form.form_1 {
  grid-template-columns: 1fr;
}
@media (min-width: 992px) {
  .admin_form.form_1 {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 992px) {
  .admin_form .full_width {
    grid-column-start: 1;
    grid-column-end: 4;
  }
}
@media (min-width: 992px) {
  .admin_form.col_2 {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .admin_form.col_2 .full_width {
    grid-column-end: 3;
  }
}

.file_preview {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin: 10px 0px;
}
.file_preview img {
  height: 100px;
  border: 1px solid rgba(195, 195, 195, 0.47);
  padding: 3px;
}

